import React from 'react';
import { Link } from 'react-router-dom';
import styles from './Pages.module.scss';

const ErrorPage = () => (
  <div className={styles.ErrorPage}>
    <h2>Oooops!</h2>
    <p>,       !</p>

    <Link to="/">
      <p className={styles.LinkHome}>,    </p>
    </Link>
  </div>
);

export default ErrorPage;
